@import url(https://fonts.googleapis.com/css?family=Roboto);
@import url(https://fonts.googleapis.com/css?family=Handlee);
@font-face {
  font-family: 'Azov Medium';
  src: url(/fuckts-maps/static/media/ofont.ru_Azov.dad53247.ttf);
  font-weight: normal;
  font-style: normal;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.pov-page .pov-page__create-input .ant-input {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}

.pov-page .pov-page__create-input .ant-btn {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.ant-alert {
  display: inline-block;
  position: absolute;
  bottom: 12px;
  z-index: 99999;
  right: 12px;
}

.paper {
  position: relative;
  width: 90%;
  max-width: 250px;
  min-width: 250px;
  height: 150px;
  background: #fafafa;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .3);
  overflow: hidden;
}

.paper__drag-place {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 34px;
  background: radial-gradient(#575450 6px, transparent 7px) repeat-y;
  background-size: 30px 30px;
  border-right: 3px solid #D44147;
  box-sizing: border-box;
  cursor: -webkit-grab;
  cursor: grab;
}

.paper-content {
  position: absolute;
  top: 30px;
  right: 0;
  bottom: 30px;
  left: 34px;
  background: linear-gradient(transparent, transparent 28px, #91D1D3 28px);
  background-size: 30px 30px;
}

.paper-content textarea {
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  line-height: 30px;
  padding: 0 10px;
  border: 0;
  outline: 0;
  background: transparent;
  color: mediumblue;
  font-family: 'Azov Medium', normal;
  font-weight: bold;
  font-size: 18px;
  box-sizing: border-box;
  z-index: 1;
}

.paper__close {
  position: absolute;
  top: 8px;
  color: #fafafa;
  left: 8px;
}

